import { TextField } from "@mui/material"
import { ListViewTitle } from "../../components"
import ButtonSquare from "../../components/ButtonSquare"
import ButtonSquareSmall from "../../components/ButtonSquareSmall"
import GroupAddTitle from "./components/GroupAddTitle"
import * as yup from 'yup';
import { useEffect, useState } from "react"
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import FormErrorMessage from "../../components/FormErrorMessage"
import { useRecoilState, useSetRecoilState } from "recoil"
import { GroupAddStep, initGroupAddStepAtom } from "../../api/atom.groupadd"

interface Step5Props {
    allEdit: boolean;
    linkGroupAddStep: (num: number) => void;
}

const Step5: React.FC<Step5Props> = (props) => {
    // Atom Recoil
    const [groupStepAtom, setGroupStepAtom] = useRecoilState<GroupAddStep>(initGroupAddStepAtom);
    const setStepUseSetRecoilState = useSetRecoilState(initGroupAddStepAtom);

    type Cost = {
        cost: string;
        payment_type: string;
    }
    const [cost, setCost] = useState<Cost>(groupStepAtom.payment_method_response);
    const costValidation = yup.object({
        cost: yup
            .string(),
    });
    const { handleSubmit, formState: { errors }, register } = useForm<Cost>({
        resolver: yupResolver(costValidation),
        mode: 'onBlur',
    });
    const onSubmit = handleSubmit((data: Cost) => {
        //console.log("account", data);
        props.linkGroupAddStep(6);
    });
    const onChangeCost = (e: React.ChangeEvent<HTMLInputElement>) => {
        setCost({
            ...cost,
            [e.target.name]: e.target.value,
        });
    };

    const [type, setType] = useState([0, 0]);
    const onChange = (value: string) => {
        const data = {
            payment_type: value
        };
        setCost({ ...cost, ...data });
    }
    useEffect(() => {
        const data = {
            payment_method_response: cost
        };
        setStepUseSetRecoilState(() => ({ ...groupStepAtom, ...data }));
    }, [cost])

    useEffect(() => {
        //console.log("groupStepAtom", groupStepAtom);
        if (groupStepAtom.payment_method_response.payment_type == "MANUAL") {
            const newType = [1, 0];
            setType({ ...type, ...newType });
        }
        if (groupStepAtom.payment_method_response.payment_type == "AUTO") {
            const newType = [0, 1];
            setType({ ...type, ...newType });
        }
    }, [groupStepAtom])

    return (
        <>
            <form onSubmit={onSubmit}>
                {/*  - title */}
                <div className="common-flex-row-wrap-space-between-vcenter"
                    style={{ marginTop: 20 }}>
                    <div className="item-lr">
                        <div className="common-flex-row-wrap-vcenter">
                            <span className="font-bold-s21-c573300"><span className="span-half-FFE197">/</span></span>
                        </div>
                    </div>
                    <div className="item-lr">
                        <div className="common-flex-row-wrap-vcenter">
                            {
                                props.allEdit === true &&
                                <ButtonSquareSmall background="#FF9100"></ButtonSquareSmall>
                            }
                            <ButtonSquareSmall background="#FF9100" onClick={() => props.linkGroupAddStep(4)}></ButtonSquareSmall>
                            <ButtonSquareSmall background="#FF9100" onClick={() => onSubmit()}></ButtonSquareSmall>
                        </div>
                    </div>
                </div>

                <GroupAddTitle background="#A7DEBD" image="/asset/img/new_cost.svg">
                        . <br />
                       10%   .<br />
                    (   )         . <br />
                                , <br />
                        N   .<br />
                </GroupAddTitle>


                <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                    <div>
                        <ListViewTitle icon="/asset/img/new_target_02.svg"></ListViewTitle>
                    </div>
                    <div>
                        <TextField id="standard-basic" style={{ width: '100%' }}
                            label="1   .(N/1)"
                            variant="standard"
                            value={cost.cost || ""}
                            {...register('cost')}
                            onChange={onChangeCost} />
                    </div>
                    {errors.cost && (
                        <FormErrorMessage errorMessage={errors.cost.message!} />
                    )}
                </div>

                <div className="media-group-grey-container" style={{ marginTop: 20 }}>
                    <div>
                        <ListViewTitle icon="/asset/img/new_target_02.svg"></ListViewTitle>
                    </div>

                    <div className="media-grid--col2__11" style={{ marginTop: 20 }}>
                        <div className="box-cgc">
                            <ButtonSquare onClick={() => onChange("MANUAL")} type={type[0]}>
                                <div className="common-flex-column-wrap-hcenter-vcenter">
                                    <span className="font-bold-s15-cffffff"></span>
                                    <span className="font-normal-s15-cffffff">  .</span>
                                    <span className="font-normal-s15-cffffff">*: -%</span>
                                </div>
                            </ButtonSquare>
                        </div>
                        <div className="box-cgc">
                            <ButtonSquare onClick={() => onChange("AUTO")} type={type[1]}>
                                <div className="common-flex-column-wrap-hcenter-vcenter">
                                    <span className="font-bold-s15-cffffff"></span>
                                    <span className="font-normal-s15-cffffff">   N .</span>
                                    <span className="font-normal-s15-cffffff">*/N:(-10%)/   </span>
                                </div>
                            </ButtonSquare>
                        </div>
                    </div>


                </div>
                <div className="media-blank-bottom"></div>
            </form>
        </>

    )

}
export default Step5


